import React from 'react'
import Img, { FluidObject } from 'gatsby-image'
import { StaticQuery, graphql } from 'gatsby'
import styled from 'styled-components'

interface IFluidImage {
  node: {
    extension: string
    relativePath: string
    childImageSharp: {
      fluid: FluidObject
    }
  }
}

const StyledDynamicIcon = styled(Img)`
  max-width: 4em;
  width: 100%;
  max-height: 4em;
  height: 100%;
`

function renderImage(file: IFluidImage) {
  return <StyledDynamicIcon fluid={file.node.childImageSharp.fluid} />
}

interface IDynamicIconProps {
  path: string
}

const DynamicIcon = function({ path }: IDynamicIconProps) {
  return (
    <StaticQuery
      query={graphql`
        query {
          images: allFile(
            filter: { extension: { regex: "/jpeg|jpg|png|gif/" } }
          ) {
            edges {
              node {
                extension
                relativePath
                childImageSharp {
                  fluid(maxWidth: 128) {
                    ...GatsbyImageSharpFluid
                  }
                }
              }
            }
          }
        }
      `}
      render={({ images }) =>
        renderImage(
          images.edges.find(
            (image: IFluidImage) => image.node.relativePath === path
          ) // returns images like this node: {extension: "png", relativePath: "gatsby-icon.png", childImageSharp: {…}}
        )
      }
    />
  )
}

/**
 * Question remains: how much impact will many (100s) of images have?
 */

export default DynamicIcon
