import React, { ReactNode } from 'react'
import styled from 'styled-components'
import { Grid } from './grid'
import { Card } from './Card'

interface ISkillCardProps {
  children?: ReactNode
  level: number
  title: string
  description: string
  time: number
}

interface IStyledIndicator {
  lit: boolean
}

const StyledIndicator = styled.div`
  padding: 0.25em;
  width: 1em;
  height: 1em;
  border-radius: 1em;
  background: ${(props: IStyledIndicator) =>
    props.lit ? '#ffb74d' : 'lightgrey'};
`

const StyledIndicatorGrid = styled.div`
  max-width: 8em;
`

export const SkillCard = ({
  children,
  level,
  title,
  description,
  time,
}: ISkillCardProps) => (
  <Card>
    <header>
      <Grid columns={2}>
        <StyledIndicatorGrid>
          <Grid columns={5}>
            <StyledIndicator lit={level >= 1}></StyledIndicator>
            <StyledIndicator lit={level >= 2}></StyledIndicator>
            <StyledIndicator lit={level >= 3}></StyledIndicator>
            <StyledIndicator lit={level >= 4}></StyledIndicator>
            <StyledIndicator lit={level === 5}></StyledIndicator>
          </Grid>
        </StyledIndicatorGrid>
        <p>
          {title} (time spent: ~{time} hours)
        </p>
      </Grid>
    </header>
    <p>{description}</p>
    <div>{children}</div>
  </Card>
)
