import React from 'react'

import Layout from '../components/layout'
import SEO from '../components/seo'
import { Link } from 'gatsby'
import { SkillCard } from '../components/SkillCard'
import DynamicIcon from '../components/DynamicIcon'
import Flex from '../components/flex'
import styled, { StyledComponent } from 'styled-components'
import { Card } from '../components/Card'
import { BackgroundColorProperty } from 'csstype'

const CardHeader = styled.header`
  margin-bottom: 2em;
`

const CardHeaderText = styled.div`
  padding-left: 2em;
`

interface ICardHeaderIconWrapperProps {
  backgroundColor: BackgroundColorProperty
}

const CardHeaderIconWrapper: StyledComponent<'div', any, ICardHeaderIconWrapperProps, never> = styled.div`
  border-radius: 999px;
  overflow: hidden;
  max-width: 4em;
  width: 100%;
  max-height: 4em;
  height: 100%;
  background: ${(props: ICardHeaderIconWrapperProps) => props.backgroundColor || '#DDD'};
  padding: .5em;
`

interface IExperienceCardProps {
  name: string
  type: string
  icon: string
  dateRange: string
  description: string
  backgroundColor: BackgroundColorProperty
}

const ExperienceCard = ({
  name,
  type,
  icon,
  dateRange,
  description,
  backgroundColor
}: IExperienceCardProps) => (
  <Card>
    <CardHeader>
      <Flex justifyContent="flex-start">
        <CardHeaderIconWrapper backgroundColor={backgroundColor}>
          <DynamicIcon path={icon} />
        </CardHeaderIconWrapper>
        <CardHeaderText>
          <h1>
            {name} ({type})
          </h1>
          <h2>{dateRange}</h2>
        </CardHeaderText>
      </Flex>
    </CardHeader>
    <main>
      <p>{description}</p>
    </main>
  </Card>
)

const ContentSeparator = styled.div`
  padding-bottom: 4rem;
`

const SkillsetPage = () => (
  <Layout>
    <SEO title="My experience & skills" />
    <ContentSeparator>
      <h1>Education</h1>
      <ExperienceCard
        name="Digital Media & Communication"
        type="HBO"
        icon="icons/hu_logo.jpg"
        dateRange="sep 2015 - feb 2019"
        description="This is where my interest in programming started, as well as my first steps into graphic design. I learned about marketing and doing user research. Halfway through my final year I decided to drop out due to lack of motivation as I wanted to pursue learning more about programming."
        backgroundColor="#298CCD"
      />
    </ContentSeparator>
    <ContentSeparator>
      <h1>Professional experience</h1>
      <ExperienceCard
        name="SmartHOTEL"
        type="Internship"
        icon="icons/smarthotel_logo.png"
        dateRange="sep 2017 - feb 2018"
        description="I worked on a data visualization dashboard to provide the company with
        insights in their key metrics, to see how their BHAG (Big, Hairy,
        Audacious Goal) was progressing. This project was built using MeteorJS,
        React (with styled-components), D3.js, GraphQL and the database of
        choise was MongoDB. It was deployed locally on an Ubuntu Server VM and
        had a C# application to collect the data running on a Windows Server VM.
        There is nothing to show for it, since it was an internal project. No
        access from outside."
        backgroundColor="#F18600"
      />
    </ContentSeparator>
    <ContentSeparator>

      <h1>So, you want to know more?</h1>
      <p>
        From my experience so far, programming is about 20% actually writing code
        and 80% "looking stuff up", gathering requirements, defining test
        scenarios. Obviously, this can vary on a daily basis. But when it comes to
        actually creating, how do I think I'd score? The time spent is a rough
        estimate and should be seen as a total of the 20 / 80 split.
      </p>
      <SkillCard
        level={4}
        title="HTML(5)"
        description="HTML, along with CSS, is obviously used in any front-end JavaScript framework. Either plainly, or through JSX. I find HTML's default margins on elements stupidly annoying and always require some sort of a 'reset' CSS file."
        time={3000}
      />
      <SkillCard
        level={4}
        title="CSS(3)"
        description="I love CSS and everything you can do with it nowadays without having to resort to JavaScript. Most notably, the CSS Grid and Flex specs. But also clip-path seems pretty cool. I always use some sort of preprocessor, be it Stylus or something like styled-components or Emotion for React."
        time={3000}
      />
      <SkillCard
        level={4}
        title="JavaScript"
        description="I feel adequate using everything above ES6, but not about working with polyfills / older versions of JavaScript. I can work with the class syntax, or just plain Objects and Functions. I get Promises and Async / Await as long as it doesn't have too many layers to it."
        time={2000}
      ></SkillCard>
      <SkillCard
        level={3}
        title="TypeScript"
        description="I absolutely love it. I understand TypeScript's limits but having the compile-time safety has proven to be a huge time-saver, especially when using the strictest tsconfig imagineable. Advanced applications of Generics are still tricky to me."
        time={600}
      ></SkillCard>
      <SkillCard
        level={2}
        title="GraphQL"
        description="Yes! Please, use it more. I think the developer experience of using GraphQL is absolutely amazing. My limited experience working with third-party REST APIs has taught really convinced me that there is a lot to love about GraphQL."
        time={400}
      />
      <SkillCard
        level={3}
        title="React"
        description="As with all view-libraries I believe that one should be able to replace the layer at any point in time. Business logic has no place in Components and should be extracted to services, so that when you might swap out the view layer, you could just rely on those exact services to provide you with the data you want to display."
        time={1000}
      />
      <SkillCard
        level={2}
        title="Angular"
        description="I love the complexity, but haven't used it much at all. The fact that it's built with TypeScript is a huge win in my books."
        time={100}
      />
      <SkillCard
        level={1}
        title="Vue"
        description="Hardly ever really touched it. It's on my to-do list to learn more about."
        time={100}
      />
      <SkillCard
        level={2}
        title="MongoDB"
        description="MongoDB being provided by default in MeteorJS' stack has made it the first DB engine I have worked with. Reading the JSON-like format used in Collections has made it easy to work with. I have fiddled with the Aggregation Framework, but not much."
        time={400}
      />
      <p>
        I would like to point out that these scores are more "relative" to each other than "absolute". Whatever the subject, there's still a lot to learn. I also know that whatever problem I may face
        dealing with code, eventually a solution will come. I also know that
        becoming an expert in any field takes at least 10.000 hours of practise.
        More often than not, I'll be browsing Medium to look for new blogposts
        regarding JS. Always trying to imrpove. Most importantly investing more
        time in JavaScript / TypeScript than any specific framework, so that it'll
        be easier to pick up whatever comes next.
      </p>
      <p>
        But, as I also mention in the <Link to="/about">about me section</Link>, I
        have some experience with other programs outside of programming.
      </p>
      <SkillCard
        level={3}
        title="Adobe Illustrator"
        description="You need me to help out with design? No problem at all. I consider myself to be able to at least take on someone else's duties during their time off. Image Trace is my secret sauce."
        time={400}
      />
      <SkillCard
        level={2}
        title="Adobe After Effects"
        description="I have only really used it to make short animations, or short compilations of videos for courses. I know that the the quality of source footage is crucial when it comes to making a decent composition. Highly enjoy working with it, for sure."
        time={300}
      />
    </ContentSeparator>
  </Layout>
)

export default SkillsetPage
